<template>
  <Loading v-if="!item" />
  <Item v-else :item="item" :gallery="gallery" :API="API"/>
  <Footer />
  <Nav />
</template>

<script>
import API from "../../api.js"
import Loading from "@/components/Loading.vue"
import Item from "@/components/Item.vue"
import Footer from "@/components/Footer.vue"
import Nav from "@/components/Nav.vue"

export default {
  name: "ProductView",
  components: {
    Loading,
    Item,
    Footer,
    Nav
  },
  data() {
    return {
      item: {},
      gallery: 0,
      API: API
    }
  },
  methods: {
    async getItem(id) {
      const resItem = await fetch(`${API}/api/products/item/${id}`)
      const data = await resItem.json()
      data.pic = `${API}/images/${data.img[0]}`
      return data
    }
  },
  async created() {
    this.item = await this.getItem(this.$route.params.id)
    this.gallery = this.item.img.length
  }
}
</script>